export const countingBMR = (kg, height, age, sex, activity, goal) => {
    const bmr = sex === "MÄ™Å¼czyzna" ?
        66.47 + (13.75 * kg) + (5 * height) - (6.75 * age)
        :
        665.09 + (9.56 * kg) + (1.85 * height) - (4.67 * age)
    const bmrWithActivity = () => {
        switch (activity) {
            case "Brak aktywnoÅ›ci":
                return bmr * 1.4;
            case "Umiarkowana":
                return bmr * 1.6;
            case "Aktywny":
                return bmr * 1.75;
            case "Wysoka aktywnoÅ›Ä‡":
                return bmr * 2;
            case "Wyczynowe":
                return bmr * 2.2;
            default:
                return;
        }
    }

    switch(goal){
        case "PrzytyÄ‡":
            const gainWeight = bmrWithActivity();
            return gainWeight + 300;
        case "SchudnÄ…Ä‡":
            const loseWeight = bmrWithActivity();
            return loseWeight - 300;
        case "UtrzymaÄ‡ wagÄ™":
            return bmrWithActivity();
        default: 
            return;
    }
}


